        />
      </div>
      <div class="header__button"></div>
    </div>
    </div>
    <swiper :options="swiperOption">
　　<swiper-slide 
          v-for="(item,index) in flash" 
          :key="index"
          >
          <img :src="item.thumb" :title="item.title">
          </swiper-slide>
