<template>
  <div class="mIndexLine">
    <div class="mIndexLine__header">
      <i></i>
      <span>热门目的地推荐</span>
      <i></i>
    </div>
    <div class="mIndexLine__blank" style="height: 0px;"></div>
    <div class="mIndexLine__nav" id="boxFixed" style="top: 0px;" :class="{'is_fixed' : isFixed}">
      <div class="nav__header">
        <div
          v-for="(item,index) in upperArr"
          :key="index"
          :class="upperNum==index?'focus':''"
          @click="upperNav(index)"
        >{{item}}</div>
      </div>
      <div class="nav__body">
        <div
          v-for="(item,index) in locArr[locNum]"
          :key="index"
          :class="index==focusNum?'focus':''"
          @click="changeLoc(index)"
        >{{item}}</div>
      </div>
    </div>
    <div class="mIndexLine__body">
      <router-link
      class="body__line" 
      v-for="(item,index) in productArr" 
      :key="index"
      to="/detail"
      @click.native="toDetail(item.pid)"
      >
        <div class="line__pictrue" :style="'background-image: url(&quot;'+item.thumb+'&quot;);'">
          <div class="line__type">{{item.product_type}} | {{item.place_label}}</div>
          <div class="line__box">
            <div class="line__price">{{item.price_label}}</div>
            <div class="line__day">{{item.days}}{{item.nights}}</div>
          </div>
        </div>
        <div class="line__content">
          <div class="line__title">{{item.product_name}}</div>
          <div class="line__desc">
            <span>{{item.product_type}}</span>
            &lt;{{item.sub_name}}
          </div>
        </div>
      </router-link>
    </div>
    <div class="mIndexLine__loading" style="display: none;">
      <i class="weui-loading"></i>
      <span
        style="vertical-align: middle; display: inline-block; font-size: 14px;"
      >&nbsp;&nbsp;正在加载中...</span>
    </div>
    <div class="mToTop" style="display: none;"></div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        productArr: [],
        upperArr: ["国内游", "出境游", "周边游"],
        locArr: [
          [
            "热门",
            "内蒙",
            "新疆",
            "西北",
            "贵州",
            "广西",
            "四川",
            "西藏",
            "云南",
            "晋南",
            "华南",
            "华北",
            "华中",
            "华东",
            "更多"
          ],
          [
            "热门",
            "中东非",
            "南亚",
            "欧洲",
            "海岛",
            "东南亚",
            "日韩朝",
            "澳新",
            "美加",
            "极地"
          ],
          [
            "热门",
            "杭州",
            "上海",
            "武汉",
            "北京",
            "成都",
            "广州",
            "苏州",
            "南京",
            "重庆"
          ]
        ],
        locNum: 0,
        focusNum: 0,
        tagId: 0,
        regId:2,
        upperNum: 0,
        isFixed:false,
      };
    },
    methods: {
      // 跳转到详情页
      toDetail(pid){
        this.$store.state.pid = pid;
      },
      // 点击地名切换数据包
      changeLoc(index) {
        this.focusNum = index;
        if (this.upperNum == 0) {
          if(index == 0) {
            this.tagId =0;
          }
          if (index == 1) {
            this.tagId = 49;
          }
          if (index == 2) {
            this.tagId = 48;
          }
          if (index == 3) {
            this.tagId = 21;
          }
          if (index == 4) {
            this.tagId = 44;
          }
          if (index == 5) {
            this.tagId = 45;
          }
          if (index == 6) {
            this.tagId = 46;
          }
          if (index == 7) {
            this.tagId = 47;
          }
          if (index == 8) {
            this.tagId = 43;
          }
          if (index == 9) {
            this.tagId = 111;
          }
          if (index == 10) {
            this.tagId = 82;
          }
          if (index == 11) {
            this.tagId = 83;
          }
          if (index == 12) {
            this.tagId = 113;
          }
          if (index == 13) {
            this.tagId = 74;
          }

        }
        else if(this.upperNum == 1) {
          if(index == 0) {
            this.tagId = 0;
          }
          if (index == 1) {
            this.tagId = 28;
          }
          if (index == 2) {
            this.tagId = 27;
          }
          if (index == 3) {
            this.tagId = 32;
          }
          if (index == 4) {
            this.tagId = 31;
          }
          if (index == 5) {
            this.tagId = 26;
          }
          if (index == 6) {
            this.tagId = 30;
          }
          if (index == 7) {
            this.tagId = 29;
          }
          if (index == 8) {
            this.tagId = 71;
          }
          if (index == 9) {
            this.tagId = 81;
          }
        }
        else if(this.upperNum == 2) {
          if(index == 0) {
            this.tagId = 0;
          }
          if (index == 1) {
            this.tagId = 1;
          }
          if (index == 2) {
            this.tagId = 2;
          }
          if (index == 3) {
            this.tagId = 4;
          }
          if (index == 4) {
            this.tagId = 5;
          }
          if (index == 5) {
            this.tagId = 6;
          }
          if (index == 6) {
            this.tagId = 7;
          }
          if (index == 7) {
            this.tagId = 10;
          }
          if (index == 8) {
            this.tagId = 13;
          }
          if (index == 9) {
            this.tagId = 14;
          }
        }
        this.$axios(`/api/api/lines?class_region_id=${this.regId}&tag_id=${this.tagId}&page=1`
        ).then(res => {
          this.productArr = res.data.data.products;
        });
      },
      upperNav(index) {
        this.upperNum=this.locNum=index;
        if(index == 0) {
          this.regId = 2;
        }else if (index == 1) {
          this.regId = 3;
        }else {
          this.regId = 1;
        }
        this.$axios(`/api/api/lines?class_region_id=${this.regId}&tag_id=${this.tagId}&page=1`
        ).then(res => {
            this.productArr = res.data.data.products;
        });
      },
      handleScroll() {
        let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;
        let offsetTop = document.querySelector("#boxFixed").offsetTop;
        if(scrollTop < 3500) {
          this.isFixed=false;
        }else {
          this.isFixed=true;
        }
      }
    },
    created() {
      this.$axios(`/api/api/lines?class_region_id=${this.regId}&tag_id=${this.tagId}&page=1`
      ).then(res => {
          this.productArr = res.data.data.products;
      });
    },
    mounted() {
      window.addEventListener("scroll", this.handleScroll);
    }
  };
</script>

<style>
  .mIndexLine .mIndexLine__header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 7.5rem;
    height: 0.74rem;
    background: #f7f7f9;
  }

  .mIndexLine .mIndexLine__header i {
    display: inline-block;
    width: 2.1rem;
    height: 0.02rem;
    background: #ededed;
  }

  .mIndexLine .mIndexLine__header span {
    margin-left: 0.4rem;
    margin-right: 0.2rem;
    padding-left: 0.4rem;
    font-size: 0.24rem;
    color: #585858;
    background: url(data:image/png;base64,)
      0 no-repeat;
    background-size: 0.33rem 0.33rem;
  }

  .mIndexLine .mIndexLine__nav {
    width: 7.5rem;
    background: #fff;
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  .is_fixed {
    position: fixed;
    z-index: 999;
  }

  .mIndexLine .mIndexLine__nav .nav__header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 7.5rem;
    height: 0.9rem;
    border-bottom: 1px solid #eee;
  }

  .mIndexLine .mIndexLine__nav .nav__header div {
    position: relative;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    height: 0.9rem;
    line-height: 0.9rem;
    text-align: center;
    font-size: 0.32rem;
    color: #000;
  }

  .mIndexLine .mIndexLine__nav .nav__header .focus:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0.05rem;
    background: #ffc31a;
  }

  .mIndexLine .mIndexLine__nav .nav__body {
    padding-top: 0.2rem;
    font-size: 0;
  }

  .mIndexLine .mIndexLine__nav .nav__body div {
    display: inline-block;
    margin: 0 0.15rem;
    margin-bottom: 0.2rem;
    width: 1.2rem;
    height: 0.55rem;
    line-height: 0.55rem;
    text-align: center;
    font-size: 0.22rem;
    color: #666;
    border-radius: 0.1rem;
    background: #f7f7f7;
  }

  .mIndexLine .mIndexLine__nav .nav__body .focus {
    color: #333;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: 1px solid #333;
    background: #fdc72e;
  }

  .mIndexLine .mIndexLine__nav--fixed {
    position: fixed;
    top: 0;
    z-index: 999;
  }

  .mIndexLine .mIndexLine__blank {
    width: 7.5rem;
  }

  .mIndexLine .mIndexLine__body .body__line {
    position: relative;
    display: block;
    margin-bottom: 0.2rem;
    width: 7.5rem;
    height: 4.84rem;
    font-size: 0;
    background: #fff;
    overflow: hidden;
  }

  .mIndexLine .mIndexLine__body .body__line:last-child {
    margin-bottom: 0;
  }

  .mIndexLine .mIndexLine__body .body__line .line__pictrue {
    width: 100%;
    height: 3.6rem;
    position: relative;
    background: #fff9db 50% no-repeat;
    background-size: cover;
  }

  .mIndexLine .mIndexLine__body .body__line .line__pictrue .line__type {
    position: absolute;
    left: 0.26rem;
    top: 0.32rem;
    padding: 0 0.2rem;
    height: 0.44rem;
    line-height: 0.44rem;
    color: #fff;
    border-radius: 0.22rem;
    background: rgba(0, 0, 0, 0.4);
  }

  .mIndexLine .mIndexLine__body .body__line .line__pictrue .line__box {
    position: absolute;
    right: 0.1rem;
    bottom: 0.2rem;
    font-size: 0;
  }

  .mIndexLine
    .mIndexLine__body
    .body__line
    .line__pictrue
    .line__box
    .line__price {
    padding: 0 0.15rem;
    min-width: 1.1rem;
    height: 0.36rem;
    line-height: 0.36rem;
    text-align: center;
    font-size: 0.26rem;
    color: #fff;
    background: #ffc31a;
  }

  .mIndexLine .mIndexLine__body .body__line .line__pictrue .line__box .line__day {
    height: 0.36rem;
    line-height: 0.36rem;
    text-align: center;
    font-size: 0.26rem;
    color: #ffc112;
    background: #fff;
  }

  .mIndexLine .mIndexLine__body .body__line .line__content {
    padding: 0 0.25rem;
    height: 1.24rem;
  }

  .mIndexLine .mIndexLine__body .body__line .line__content .line__title {
    padding-top: 0.15rem;
    font-size: 0.28rem;
    color: #000;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .mIndexLine .mIndexLine__body .body__line .line__content .line__desc {
    margin-top: 0.15rem;
    width: 100%;
    font-size: 0.26rem;
    color: #b4b4b4;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .mIndexLine .mIndexLine__body .body__line .line__content .line__desc span {
    display: inline-block;
    margin-right: 0.1rem;
    padding: 0 0.1rem;
    height: 0.35rem;
    line-height: 0.35rem;
    vertical-align: 1px;
    text-align: center;
    font-size: 0.26rem;
    font-style: normal;
    color: #fff;
    background: #f68b6e;
  }

  .mIndexLine .mIndexLine__loading {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    padding: 0.1rem 0;
  }
</style>